import React, { useState } from 'react';
import '../static/css/skills.css';


const SkillItem = (props) => {
    return (
        <div className="skill-item-container">
            <figure className="skill-item-figure">
                <img className="skill-item-image" src={props.imageSrc} />
                <figcaption className="skill-item-figure-caption">{props.skillArea}</figcaption>
                <div className="skills-list-container">
                    <ul className="skills-list">
                        {props.skillsArray.map((skillItem, index) => {
                            return <li key={index}>{skillItem}</li>
                        })}
                    </ul>
                </div>
            </figure>
        </div>
    )
}

export default SkillItem;